<template>
  <div v-if="audits && audits.length"> 

    <div class="btn-group btn-group-sm">
      <button type="button" class="btn btn-light">All</button>
      <button type="button" class="btn btn-light">Past Due</button>
      <button type="button" class="btn btn-light">Current</button>
      <button type="button" class="btn btn-light">Future Dated</button>
    </div>

    <table class="table table-hover">
      <thead>
        <tr>
          <th>Aduit</th>
          <th>Start Date</th>
          <th>Due Date</th>
          <th>Frequency</th>
          <th>Options</th>
        </tr>
      </thead>
      <tbody>
          
        <tr v-for="(audit, index) in audits" :key="index" @click="redirectToAudit(audit.audit_type.name)"
          v-bind:class="{'table-danger' : new Date(audit.due_date) < new Date(), 'table-info' : new Date(audit.start_date) > new Date()}">
            <td v-if="audit.client === 0 || audit.client === 1">{{ audit.audit_type.name }}</td>
            <td v-else>{{ audit.client_value }} {{ audit.audit_type.name }}</td>
            <td>{{ formatDate(audit.start_date) }}</td>
            <td>{{ formatDate(audit.due_date) }}</td>
            <td>{{ audit.audit_type.frequency }}</td>
            <td>
              <a class="btn btn-light btn-sm edit-link" :href="getEditAuditURL(audit.id)"><span class="material-icons">edit</span></a>
              <button class="btn btn-light btn-sm delete-link" @click="confirmDeleteAudit($event, audit.id)"><span class="material-icons">delete</span></button> 
            </td>
        </tr>
          
      </tbody>
    </table>

    <ul class="pagination justify-content-center">
      <li class="page-item"><a class="page-link" href="#">Prev</a></li>
      <li class="page-item"><a class="page-link" href="#">1</a></li>
      <li class="page-item"><a class="page-link" href="#">2</a></li>
      <li class="page-item"><a class="page-link" href="#">3</a></li>
      <li class="page-item"><a class="page-link" href="#">Next</a></li>
    </ul>

  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'AuditList',
  data() {
    return {
      audits: [], 
      today: null,
      page: 1,
      pageSize: 20,
      totalAudits: 0,
    };
  },

  created() {
    this.fetchAudits();
    this.today = new Date();
  },

  computed: {
    totalPages() {
      return Math.ceil(this.totalAudits / this.pageSize)
    },
  },

  methods: {
    async fetchAudits() {
      try {
        const response = await axios.get('http://localhost:8000/riskmanagement/api/audits/', {
          params: {
            page: this.page,
            page_size: this.pageSize,
          }
        });
        this.audits = response.data.results;
        this.totalAudits = response.data.count;
      } catch (error) {
        console.error("Error fetching data: ", error);
      }
    },

    formatDate(date) {
      const javadate = new Date(date)
      const formattedDate = `${javadate.getMonth() + 1}-${javadate.getDate()}-${javadate.getFullYear()}`;
      return formattedDate;
    },

    getEditAuditURL(auditId) {
      return `/riskmanagement/edit_audit/${auditId}/`;
    },

    async confirmDeleteAudit(event, auditId) {
      event.preventDefault;
      if (confirm("Are you sure you want to delete this audit?")) {
        window.location.href = `/riskmanagement/delete_audit/${auditId}/`;
      }
    },

    redirectToAudit(auditType) {
      const audit_string = auditType.toLowerCase().replace(/ /g, "_");
      console.log(audit_string);
      const url = `/riskmanagement/${audit_string}/`;
      window.location.href = url;
    },
  },
}
</script>

